import "./Register.css";
import React from 'react';
import { Form, Button, Col } from "react-bootstrap";
import { Link } from "react-router-dom";
import eventManager from './CLASSES/EventManager';
import client from './CLASSES/client';
import since from "./Functions/since";
import validation from "./Functions/validation";
// import Button from '@material-ui/core/Button';

class Register extends React.Component {
    username = "";
    email = "";
    // password = "";
    // tribe = -1;
    // server = 1;
    // state = { error: false }
    // error = { username: "", email: "", password: "" }
    /* state:
    0 = Getting server state for registeration.
    1 = Registeration is available.
    2 = Server is full, cannot register.
    3 = Registeration is done.
    4 = Server is not ready.
    5 = Server has ended.
    */
    haveRegistered = false;
    constructor(props) {
        super(props);
        eventManager.create("registered");
    }
    componentDidMount() {
        eventManager.listen("registered", () => {
            //...
        });
    }
    register(username = "", email = "") {

    }
    render() {
        if (this.haveRegistered)
            return <RegisterationDone username={this.username} email={this.email} />;
        else
            return <RegisterationForm onRegister={this.register} />
    }
}

class RegisterationDone extends React.Component {
    render() {
        return <div className="game-register">
            <h1>التسجيل</h1>
            <div className="game-register-top">
                <div><b>تم التسجيل</b></div>
                <div>مرحباً <b>{this.props.username}</b> تم التسجيل في السيرفر</div>
                <div>يرجى الاحتفاظ في بريدكم الإلكرتوني <b dir="ltr">{this.props.email}</b> في حال شراء الذهب</div>
                <div>حتى يمكنكم إستخدام نفس الذهب المشترى في سيرفرات أخرى</div>
                <br />
                <div>يمكنكم الآن <Link to="/Login">تسجيل الدخول</Link> والبدأ في اللعب</div>
                <div>نتمنى لكم أوقات ممتعة.</div>
            </div>
        </div>;
    }
}

class RegisterationForm extends React.Component {
    username = "";
    email = "";
    password = "";
    tribe = 0;
    serverNumber = 0;
    state = { error: false, ready: false, update: 0 }
    error = { username: "", email: "", password: "", tribe: "", server: "" }

    clearError() {
        this.error.username = "";
        this.error.email = "";
        this.error.password = "";
        this.error.tribe = "";
        this.error.server = "";
    }

    handleOnSubmit(e) {
        e.preventDefault();
        this.clearError();
        let err = false;
        let e_username = validation.safeUsername(this.username);
        let e_email = validation.safeEmail(this.email);
        let e_password = validation.safePassword(this.password);
        if (!e_username.valid) {
            this.error.username = e_username.result;
            err = true;
        }
        if (!e_email.valid) {
            this.error.email = e_email.result;
            err = true;
        }
        if (!e_password.valid) {
            this.error.password = e_password.result;
            err = true;
        }
        if (this.tribe < 1 || this.tribe > 5) {
            this.error.tribe = "(الرجاء أختيار القبيلة)";
            err = true;
        }
        if (this.serverNumber < 1 || this.serverNumber > 4) {
            this.error.server = "(الرجاء أختيار السيرفر)";
            err = true;
        }
        if (err) {
            this.setState({ error: true, update: Date.now() });
            return false;
        }
        //client.register(this.username, this.email, this.password, this.tribe, this.serverNumber);
        return false;
    }
    handleOnSelect(serverNumber = 1) {
        this.serverNumber = serverNumber;
    }

    tribeInput(value = "", name = "") {
        return <label><input type="radio" name="tribe" value={value} onChange={e => (this.tribe = Number(e.target.value))} /> <span>{name}</span></label>;
    }

    componentDidMount() {

    }

    render() {
        return <div className="game-register">
            <h1>التسجيل</h1>
            <div className="game-register-top">
                <div><span className="green">التسجيل</span></div>
                <p>قبل التسجيل يرجى قراءة <Link to="/Rules">التعليمات</Link> لمعرفة مزايا ومساوئ القبائل</p>
            </div>
            <form onSubmit={e => this.handleOnSubmit(e)}>
                <Form.Group className="game-register-form">
                    <Form.Row>
                        <Form.Label column sm={2}>اسم المستخدم</Form.Label>
                        <Col>
                            <Form.Control tabIndex={1} name="name" type="text" size="sm" onChange={e => (this.username = e.target.value)} placeholder="اسم المستخدم" />
                            <div className="error ta-right">{this.error.username}</div>
                        </Col>
                    </Form.Row>
                    <Form.Row>
                        <Form.Label column sm={2}>البريد الالكتروني</Form.Label>
                        <Col>
                            <Form.Control tabIndex={2} name="email" type="email" size="sm" onChange={e => (this.email = e.target.value)} placeholder="البريد الالكتروني" />
                            <div className="error ta-right">{this.error.email}</div>
                        </Col>
                    </Form.Row>
                    <Form.Row>
                        <Form.Label column sm={2}>كلمة المرور</Form.Label>
                        <Col>
                            <Form.Control tabIndex={3} name="password" type="password" onChange={e => (this.password = e.target.value)} size="sm" placeholder="كلمة المرور" />
                            <div className="error ta-right">{this.error.password}</div>
                        </Col>
                    </Form.Row>
                    <Form.Row>
                        <Col><hr /></Col>
                    </Form.Row>
                    <Form.Row className="tribe">
                        <Col style={{ textAlign: "right" }}>
                            <div className="green">إختر القبيلة<div className="error">{this.error.tribe}</div></div>
                            <br />
                            <div style={{ textAlign: "center" }}>
                                {this.tribeInput(1, "الرومان")}
                                {this.tribeInput(2, "الجرمان")}
                                {this.tribeInput(3, "الاغريق")}
                                {this.tribeInput(4, "العرب")}
                                {this.tribeInput(5, "الفراعنة")}
                            </div>
                        </Col>
                    </Form.Row>
                    <Form.Row>
                        <Col><hr /></Col>
                    </Form.Row>
                    <Form.Row className="tribe">
                        <Col style={{ textAlign: "right" }}>
                            <div className="green">إختر السيرفر<div className="error">{this.error.server}</div></div>
                            <br />
                            <div style={{ textAlign: "center", overflowX: "visible" }}>
                                <ServersList onSelect={s => this.handleOnSelect(s)} />
                            </div>
                        </Col>
                    </Form.Row>
                    <Form.Row>
                        <Col><hr /></Col>
                    </Form.Row>
                </Form.Group>
                <Button variant="secondary" type="submit">تسجيل</Button>
            </form>
        </div>;
    }
}

class ServersList extends React.Component {
    selected_server = 1;
    servers = null;
    list = null;
    state = { ready: false }
    constructor(props) {
        super(props);
        client.listServers().then(response => {
            this.servers = response;
            this.list = response.map(server => <ServerCard key={server.serverNumber + "_" + server.started} data={server} onSelect={s => this.props.onSelect(s)} />);
            this.setState({ ready: true });
        });
    }
    render() {
        if (!this.state.ready)
            return <></>;
        else
            return this.list;
    }
}

/** @param {{data: { serverNumber: number, ready: boolean, ended: boolean, isFull: boolean, started: number, players: number }}} props */
function ServerCard(props) {
    let data = props.data;
    return <label className="server-card">
        <input type="radio" name="server" value={data.serverNumber} onChange={e => props.onSelect(Number(e.target.value))} />
        <div className="card-holder">
            <div className="server-card-header"><input type="checkbox" checked />السيرفر <b>{data.serverNumber}</b></div>
            <div className="server-card-content">
                <div>عدد اللاعبين: <b>{data.players}</b></div>
                <div>بدأ منذ <b>{since(data.started)}</b></div>
                <div>
                    {
                        data.ready ? <b style={{ color: "green" }}>جاهز للعب</b> :
                            <b className="error">{
                                data.ended ? "منتهي" :
                                    data.isFull ? "ممتلئ" :
                                        "لم يبدأ بعد"
                            }</b>
                    }
                </div>
            </div>
        </div>
    </label>
}


